<template>
  <div class="side-container">
    <el-row class="tac">
      <el-col :span="30">
        <el-menu
          default-active="2"
          class="el-menu-vertical-demo"
          @open="handleOpen"
          @close="handleClose"
          @select="handleSelect"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
        >
          <el-menu-item :index="recommand">
            <template slot="title">
              <img src="../assets/recommend.png" class="img-class" alt="" />
              <span>推荐</span>
            </template>
          </el-menu-item>
          <el-menu-item :index="buyShoeUrl">
            <template slot="title">
              <img src="../assets/shoe.png" class="img-class" alt="" />
              <span>鞋子</span>
            </template>
          </el-menu-item>
          <el-menu-item :index="buyPhoneUrl">
            <img src="../assets/phone.png" class="img-class" alt="" />
            <span slot="title">手机</span>
          </el-menu-item>
          <el-menu-item :index="buyRecordUrl">
            <img src="../assets/buyRecord.png" class="img-class" alt="" />
            <span slot="title">购买记录</span>
          </el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  props:{
    userId: {
      type: Number,
    },
  },
  computed:{
    buyShoeUrl(){
      return "/shoe?type=shoe&userId="+this.userId;
    },
    buyPhoneUrl(){
      return "/phone?type=phone&userId="+this.userId;
    },
    buyRecordUrl(){
      return "/buyRecord?userId="+this.userId;
    },
    recommand(){
      return "/recommand?userId="+this.userId;
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      this.$router.push({
        path: key,
        params: { data: "query" },
      });
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>

<style lang="scss" scoped>
.side-container {
  margin-top: 100px;
}
.img-class {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}
/deep/ .el-menu {
  height: 600px;
  width: 200px;
  
}
/deep/ .el-menu-item {
  font-size: 15px;
}
</style>
