<template>
  <el-table :data="data" style="width: 100%;margin-top:60px">
    <el-table-column prop="name" label="" width="100"> </el-table-column>
    <el-table-column label="" width="100"> 
      <template slot-scope="scope">
        <img :src="scope.row.url" style="height:50px" alt="">
      </template>
    </el-table-column>
    <el-table-column prop="brand" label="" width="100"> </el-table-column>
    <el-table-column prop="description" label="" width="600"> </el-table-column>
    <el-table-column prop="price" label="" width="180"> </el-table-column>
    <el-table-column prop="time" label=""> </el-table-column>
  </el-table>
</template>
<script>
import { GetBuyRecord } from "./api.js";
import {Formate} from "../utils/DateUtils.js"
export default {
  data() {
    return {
      type: "",
      data: [],
      dialogVisible: false,
      selectedMall: {},
      urls: [],
      userId:""
    };
  },
  async mounted() {
    this.userId = this.$route.query.userId;
    this.data = await GetBuyRecord({ userId: this.userId });
    this.data.forEach((item)=>{
      let time = new Date(item.time).getTime();
      item.time = Formate(time, "YYYY-mm-dd HH:MM:SS");
    })
    console.log(this.data);
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
.img-box {
  flex-wrap: wrap;
  margin-right: 100px;
  margin-left: 100px;
  margin-top: 100px;
}

.item-class {
  margin-right: 100px;
  margin-bottom: 50px;
  cursor: pointer;
}
.des-class {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
}
</style>