<template>
  <div class="recommand-cotainer">
    <div flex class="img-box" v-if="malls && malls.length > 0">
      <div
        v-for="item in malls"
        :key="item.id"
        class="item-class"
        @click="clickMall(item)"
      >
        <img :src="item.url" alt="" style="width: 100px; height: 100px" />
        <div style="color: red">￥{{ item.price }}</div>
        <div class="des-class">{{ item.description }}</div>
      </div>
    </div>
    <div v-else>
      <img src="../assets/empty.png" alt="" class="img-class" />
      <div style="margin-left: 560px; margin-top: 20px">暂无推荐</div>
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" width="40%">
      <el-carousel indicator-position="outside">
        <el-carousel-item v-for="(url, index) in urls" :key="index">
          <img :src="url" alt="" style="height: 100%; width: 100%" />
        </el-carousel-item>
      </el-carousel>
      <div style="color: red; font-size: 24px">￥{{ selectedMall.price }}</div>
      <div>{{ selectedMall.description }}</div>
      <div style="margin: 20px"></div>
      <el-button type="primary" @click="buy">购买</el-button>
    </el-dialog>
  </div>
</template>

<script>
import { GetRecommendMalls, GetMallDetail, BuyMall } from "./api";
export default {
  data() {
    return {
      malls: [],

      dialogVisible: false,
      selectedMall: {},
      urls: [],
      userId: "",
      timer: "",
    };
  },

  components: {},
  activated() {},
  async mounted() {
    this.userId = this.userId = this.$route.query.userId;
    if(this.userId){
      this.malls = await GetRecommendMalls({ userId: this.userId });
    }
    if (!this.timer) {
      timer = window.setInterval(async () => {
        if (!this.userId) {
          return;
        }
        this.malls = await GetRecommendMalls({ userId: this.userId });
      }, 5000);
    }
  },
  methods: {
    async clickMall(mall) {
      this.selectedMall = mall;
      let detail = await GetMallDetail(this.type, mall.id);
      console.log(detail);
      this.urls = detail.urls.split(",");
      this.dialogVisible = true;
    },
    async buy() {
      await BuyMall({
        userId: this.userId,
        mallId: this.selectedMall.id,
      });
      this.$message({
        message: "购买成功",
        type: "success",
      });
      this.dialogVisible = false;
    },
  },
};
</script>

<style scoped lang="scss">
.recommand-cotainer {
}

.img-box {
  flex-wrap: wrap;
  margin-right: 100px;
  margin-left: 100px;
  margin-top: 100px;
}
.img-class {
  margin-left: 500px;
  margin-top: 200px;
  opacity: 0.6;
}

.item-class {
  margin-right: 100px;
  margin-bottom: 50px;
  cursor: pointer;
}
.des-class {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 200px;
}
</style>
